// frontend/pages/admin/index.js
import { useEffect, useState } from 'react';
import AdminSidebar from '../../components/AdminSidebar';
import AdminTopbar from '../../components/AdminTopbar';
import api from '../../utils/api';

export default function AdminHome() {
  const [stats, setStats] = useState({ pending: 0, approved: 0, transactions: 0 });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    let mounted = true;
    async function fetchStats() {
      try {
        // pending posts
        const pendingRes = await api.get('/admin/pending-posts');
        const pendingCount = pendingRes.data?.length || 0;

        // approved posts (use posts/approved)
        const approvedRes = await api.get('/posts/index');
        const approvedCount = approvedRes.data?.length || 0;

        // transactions
        const txRes = await api.get('../transactions');
        const txCount = txRes.data?.length || 0;

        if(mounted) setStats({ pending: pendingCount, approved: approvedCount, transactions: txCount });
      } catch (err) {
        console.error('Admin stats error', err);
      } finally { if(mounted) setLoading(false); }
    }
    fetchStats();
    return () => { mounted = false; };
  }, []);

  return (
    <div className="min-h-screen bg-gray-50">
      <AdminTopbar title="Admin Dashboard" />
      <div className="max-w-7xl mx-auto p-6 flex gap-6">
        <AdminSidebar />
        <main className="flex-1">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <div className="p-5 bg-white rounded shadow">
              <div className="text-sm text-gray-500">Pending Posts</div>
              <div className="text-3xl font-bold">{loading ? '...' : stats.pending}</div>
            </div>
            <div className="p-5 bg-white rounded shadow">
              <div className="text-sm text-gray-500">Approved Listings</div>
              <div className="text-3xl font-bold">{loading ? '...' : stats.approved}</div>
            </div>
            <div className="p-5 bg-white rounded shadow">
              <div className="text-sm text-gray-500">Transactions</div>
              <div className="text-3xl font-bold">{loading ? '...' : stats.transactions}</div>
            </div>
          </div>

          <div className="bg-white rounded shadow p-4">
            <h2 className="text-lg font-semibold mb-3">Recent activity</h2>
            <p className="text-sm text-gray-600">Use the Pending and Transactions pages to manage items and payments.</p>
          </div>
        </main>
      </div>
    </div>
  );
}
